@import url("https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@100;400;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Overpass:wght@300;600&family=Sora:wght@100;300;400;500;600;700&display=swap");

$font: "Sora";
$accent-color: #59acb5;
$primary-color: #e0fcff;
$secondary-color: #3d8fa8;
$tertiary-color: #1f2833;
$gray-color: #c5c6c7;
$light-gray: #e9e9e9;

@keyframes fadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 100%;
  }
}

@keyframes bounce {
  0%,
  40% {
    transform: translateY(0);
  }
  20% {
    transform: translateY(-20px);
  }
}

@keyframes bounce-down {
  0%,
  40% {
    transform: translateY(0);
  }
  20% {
    transform: translateY(20px);
  }
}

@mixin center {
  display: flex;
  justify-content: center;
  align-items: center;
}

@mixin columnFlex {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

@mixin sectionBackground {
  background-repeat: no-repeat;
  background-attachment: fixed;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

html {
  width: 100%;
  margin: 0;
}

body {
  // overflow: hidden;
  width: 100%;
  max-width: 100vw;
  font-family: $font;
  min-height: 100vh;
}

.navbar {
  overflow: hidden;
  position: fixed;
  display: flex;
  flex-direction: column;
  transform: translateX(100%);
  background-color: white;
  font-weight: 700;
  font-size: 1rem;
  height: 100%;
  width: 100vw;
  top: 0;
  z-index: 5;
  padding: 1rem 2rem;
  transition: all 250ms ease-in-out;

  &.open {
    transform: translateX(0);
  }

  .nav-links {
    display: flex;
    flex-direction: column;
    gap: 2em;
    margin: auto;
  }

  .nav-link {
    padding: 1em;

    &:active {
      color: $accent-color;
      background-color: rgba($color: $accent-color, $alpha: 0.5);
    }
  }

  .nav-link__content {
    display: flex;
    gap: 1em;
  }

  .navbar-menu-icon {
    cursor: pointer;
    align-self: flex-end;
    width: 36px;
  }

  img {
    width: 24px;
  }

  a {
    display: flex;
    align-items: center;
    gap: 1em;
    color: black;
    text-decoration: none;

    &:visited {
      color: black;
    }
  }

  &.sticky {
    background-color: rgba($color: white, $alpha: 0.8);
  }
}

.section-title {
  @include columnFlex();
  font-size: 1.5rem;
  text-align: center;
  height: 100vh;
  transition: all 0.5s ease-in-out;

  h2 {
    font-weight: 400;
  }
}

.underline {
  width: 8rem;
  height: 0.35rem;
  margin-top: 1rem;
  margin-bottom: 1.25rem;
  background: $accent-color;
  margin-left: auto;
  margin-right: auto;
}

// Header

.header {
  position: relative;
  overflow: hidden;
  @include columnFlex();
  background-color: white;
  padding: 1rem 2rem;
  justify-content: flex-start;

  .navbar-menu-icon {
    cursor: pointer;
    align-self: flex-end;
    width: 36px;
    height: auto;
  }

  .down-icon {
    align-self: flex-end;
    animation: bounce 2s infinite;
    transition: transform 0.5s ease-in-out, filter 0.25s ease-in-out;

    &:hover {
      animation-play-state: paused;
      filter: brightness(50%);
    }
  }
}

.header-section-center {
  justify-self: center;
  width: 100%;
  max-width: 400px;
  margin: auto 0;
  padding: 0 1em;

  display: flex;
  flex-direction: column;

  animation: fadeIn 500ms ease-in;

  h1 {
    font-weight: 400;
    display: inline-block;
  }

  strong {
    font-weight: 400;
    color: $accent-color;
  }

  h3 {
    font-weight: 500;
  }
}

.header-line {
  border-bottom: 5px solid black;
  width: 50%;
  margin-bottom: 2em;
  transform: translateX(0);
  transition: all 0.75s ease-in-out;

  &.preload {
    visibility: none;
    transform: translateX(-100vw);

    &:last-of-type {
      transform: translateX(100vw);
    }
  }

  &:last-of-type {
    align-self: flex-end;
    margin-top: 2em;
    margin-bottom: 0;
  }
}

// About

#about {
  background-color: #000000;
  color: white;

  .section-title {
    color: white;
    background-image: url("./images/account_background.png");
    background-repeat: no-repeat;
    background-position: top 50% right;
  }
}

.about-center {
  margin: 0 auto;
  max-width: 600px;
  padding: 4em 1.5em 10em;

  h2 {
    color: $accent-color;
    font-size: 1.125rem;
    margin-bottom: 1.25em;
  }

  p {
    font-size: 0.75rem;
    line-height: 1.8em;

    &:last-of-type {
      margin-bottom: 4em;
    }
  }
}

.skills-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  row-gap: 1em;
}

.item {
  font-size: 0.8em;
  &::marker {
    color: $accent-color;
  }
}

// Experience

#experience {
  background-color: white;

  .section-title {
    background-image: url("./images/office_bag_bg.png");
    background-position: top 40% left;
    background-repeat: no-repeat;
  }
}

.experience-center {
  margin: 0 auto;
  max-width: 600px;
  padding: 4em 1.5em;

  .btn-container {
    display: flex;
    justify-content: space-between;
  }

  .divider-line {
    border-top: 3px solid black;
    width: 100%;
  }

  .experience-btn {
    cursor: pointer;
    flex: 1 1 auto;
    padding: 0.5em;
    font-size: 1em;
    font-weight: 600;
    position: relative;
    outline: none;
    border: none;
    background-color: transparent;
    transition: transform 0.2s ease-in-out;

    &:focus,
    &:active {
      color: $accent-color;
    }

    &:hover {
      // color: $accent-color;
      transform: translateY(-10px);
      text-decoration: underline;
      text-decoration-color: $accent-color;
      text-decoration-thickness: 3px;
    }

    &.active-btn {
      // background-color: rgba($color: $accent-color, $alpha: 0.25);
      color: $accent-color;
    }
  }
}

.experience-info {
  padding: 2em 1em;
  transition: all 0.4s ease-in-out;

  h3 {
    color: $accent-color;
    font-weight: 600;
    letter-spacing: 0.125em;
  }

  h4 {
    font-size: 1.25em;
    margin-bottom: 1em;
  }

  .experience-date {
    color: #777777;
    margin-bottom: 1em;
  }

  .description {
    display: flex;
    gap: 1em;
  }

  .description + .description {
    margin-top: 1em;
  }

  .job-icon {
    padding-top: 2px;
    color: $secondary-color;
  }

  .job-desc {
    line-height: 1.5rem;
  }
}

// Projects

#projects {
  .section-title {
    background-image: url("./images/monitor_bg.png");
    background-position: top 50% right;
    background-repeat: no-repeat;
  }

  .section-center {
    padding: 4em 1.5em;

    @include columnFlex();
    gap: 10rem;
  }
}

.project {
  @include columnFlex();
  align-items: flex-start;

  img {
    width: 275px;
    align-self: center;
    transition: transform 0.2s ease-in-out;

    &:hover {
      transform: scale(105%);
    }
  }
}

.project__content {
  padding: 1em 10px;
  max-width: 275px;

  h3 {
    color: $accent-color;
    margin-bottom: 0.5em;
  }

  p {
    margin-bottom: 2em;
  }
}

.project__tools {
  display: flex;
  gap: 1em;
}

.tool {
  color: #c7c7c7;
  background-color: black;
  padding: 0.25em 0.5em;
  border-radius: 0.35em;
}

// Contact

#contact {
  background-color: black;
  background-image: url("./images/message_bg.png");
  background-position: top 25% left;
  background-repeat: no-repeat;
  padding-top: 40vh;

  text-align: center;
  color: white;
  gap: 15vh;

  .section-title {
    height: auto;
    color: white;
  }

  .center-container {
    h3 {
      font-size: 1em;
      font-weight: 400;
      margin-bottom: 2em;
    }

    a {
      color: $accent-color;

      &:visited {
        color: $accent-color;
      }
    }
  }

  .social-media {
    margin-top: 15vh;
    p {
      font-size: 0.75em;
    }
  }

  .sns-links {
    margin-top: 2em;

    a + a {
      margin-left: 2em;
    }

    img {
      width: 30px;
      transition: transform 0.2s ease-in-out;

      &:hover {
        transform: scale(110%);
        filter: invert(1) invert(67%) sepia(42%) saturate(427%)
          hue-rotate(138deg) brightness(86%) contrast(88%);
      }
    }
  }
}

.hidden {
  opacity: 0;

  &.left {
    transform: translateX(-100vw);
  }

  &.right {
    transform: translateX(100vw);
  }
}

@media screen and (min-width: 875px) {
  .section-title {
    background-size: 30% auto;
  }

  .navbar {
    background: rgba($color: white, $alpha: 0.5);
    backdrop-filter: blur(5px);
    position: sticky;
    transform: translateX(0);
    height: fit-content;
    width: 100%;
    display: flex;
    justify-content: flex-end;
    padding: 0 2rem;

    img {
      display: none;
    }

    .nav-links {
      flex-direction: row;
      margin: 0;
      margin-left: auto;
      gap: 0;
    }

    .nav-link {
      padding: 1rem 2rem;
      padding-top: calc(1rem + 3px);
      font-size: 0.75rem;
      transition: all 0.2s ease-in-out;
      border-bottom: 3px solid rgba($color: $accent-color, $alpha: 0);

      &:hover {
        background-color: rgba(255, 255, 255, 0.25);
        transform: translateY(-5px);
        border-bottom: 3px solid $accent-color;
      }
    }
  }

  .navbar-menu-icon {
    display: none;
  }

  // About

  #about {
  }

  .skills-grid {
    grid-template-columns: repeat(4, 1fr);
  }

  .item {
    padding-left: 2em;
  }

  #projects {
    .section-center {
      margin: 0 auto;
      max-width: 800px;
      display: grid;
      grid-template-columns: repeat(2, 1fr);
    }
  }

  .project {
    margin: 0 auto;
    justify-content: flex-start;

    img {
      align-self: flex-start;
    }
  }

  .project__content {
    p {
      line-height: 1.6em;
    }
  }

  #contact {
    background-size: 30% auto;

    .center-container {
      padding: 0 2rem;
      display: flex;
      flex-direction: column;

      h3,
      a {
        font-size: 0.8em;
      }

      img {
        width: 24px;
      }

      .down-icon {
        align-self: flex-end;
        animation: bounce-down 2s infinite;
        transition: transform 0.5s ease-in-out, filter 0.25s ease-in-out;
        &:hover {
          animation-play-state: paused;
          filter: brightness(150%);
        }

        img {
          transform: rotate(180deg);
          width: 60px;
        }
      }
    }
  }
}
